.sobre-container {
    margin: 20px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .sobre-container h1,
  .sobre-container h2 {
    font-size: 24px;
    margin-bottom: 15px;
  }
  
  .sobre-container p,
  .sobre-container ul {
    font-size: 18px;
    margin-bottom: 15px;
  }
  
  .sobre-container ul {
    list-style-type: none;
    padding-left: 0;
  }
  
  .sobre-container ul li {
    margin-bottom: 10px;
  }
  