.forgot-password-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f5f5f5;
}

.user-icon {
  width: 100px;
  height: 100px;
  margin-bottom: 20px;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
  color: black;
  font-weight: 700;
}

.forgot-password-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.forgot-password-input {
  width: 350px;
  padding: 10px;
  margin: 10px 0;
  border: none;
  border-radius: 20px;
  color: rgb(0, 0, 0);
  text-align: center;
  font-size: 16px; /* Aumentar a fonte para melhor acessibilidade */
}

.forgot-password-button {
  width: 150px;
  padding: 10px;
  margin: 10px 0;
  border: none;
  border-radius: 20px;
  background-color: #29b6f6;
  color: white;
  cursor: pointer;
  font-size: 16px; /* Aumentar a fonte para melhor acessibilidade */
  transition: background-color 0.3s ease-in-out; /* Animação suave para o botão */
}

.forgot-password-button:hover {
  background-color: #0288d1;
}

.success-message {
  margin-top: 20px;
  color: green;
  font-size: 16px;
}
