/* Estilos principais */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f5f5f5;
  position: relative; /* Adicionando posição relativa para o posicionamento absoluto dos filhos */
}

.user-icon {
  width: 100px;
  height: 100px;
  margin-bottom: 20px;
  transition: transform 0.3s ease-in-out; /* Adicionando animação suave */
}

.user-icon:hover {
  transform: scale(1.1);
}

.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-input {
  width: 100%;
  max-width: 350px;
  padding: 10px;
  margin: 10px 0;
  border: none;
  border-radius: 20px;
  color: rgb(0, 0, 0);
  text-align: center;
  font-size: 16px; /* Aumentar a fonte para melhor acessibilidade */
  transition: border-color 0.3s ease-in-out; /* Animação de transição suave para o erro */
}

.large-text-input {
  text-transform: uppercase; /* Transformar texto em maiúsculas */
}

.input-error {
  border: 2px solid red;
}

.error-message {
  color: red;
  font-size: 14px;
  margin-top: -10px;
}

.login-button {
  width: 100px;
  padding: 10px;
  margin: 10px 0;
  border: none;
  border-radius: 20px;
  background-color: #29b6f6;
  color: white;
  cursor: pointer;
  font-size: 16px; /* Aumentar a fonte para melhor acessibilidade */
  transition: background-color 0.3s ease-in-out; /* Animação suave para o botão */
}

.help-button {
  position: absolute; /* Posicionamento absoluto */
  top: 10px;
  left: 10px;
  padding: 10px;
  background-color: #ffa500;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 16px; /* Aumentar a fonte para melhor acessibilidade */
  transition: background-color 0.3s ease-in-out; /* Animação suave para o botão */
}

.forgot-password-link {
  font-size: 14px; /* Fonte menor */
  margin: 10px 0; /* Espaçamento abaixo do campo de senha */
  color: #29b6f6;
  text-decoration: none;
}

.forgot-password-link:hover {
  text-decoration: underline;
}

.accessibility-options {
  position: absolute; /* Posicionamento absoluto */
  top: 10px;
  right: 10px; /* Posicionar no canto superior direito */
}

.accessibility-button {
  margin: 5px;
  padding: 10px;
  background-color: #29b6f6;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 16px; /* Aumentar a fonte para melhor acessibilidade */
  transition: background-color 0.3s ease-in-out; /* Animação suave para o botão */
}

.loader {
  margin-top: 10px;
  border: 4px solid #f3f3f3;
  border-radius: 50%;
  border-top: 4px solid #29b6f6;
  width: 24px;
  height: 24px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.accessibility-button:hover, .login-button:hover, .help-button:hover {
  background-color: #0288d1;
}

/* Estilos para modo de alto contraste */
.high-contrast {
  background-color: #000 !important;
  color: #fff !important;
}

.high-contrast .login-input,
.high-contrast .login-button,
.high-contrast .help-button,
.high-contrast .register-link,
.high-contrast .accessibility-button {
  background-color: #000 !important;
  color: #fff !important;
  border: 1px solid #fff !important;
}

/* Estilos para texto ampliado */
.large-text {
  font-size: 20px;
  text-transform: uppercase; /* Transformar texto em maiúsculas */
}

.large-text-link {
  text-transform: uppercase; /* Transformar texto em maiúsculas */
}

.register-link, .forgot-password-link {
  margin-top: 10px;
  color: #29b6f6;
  text-decoration: none;
  font-size: 16px; /* Aumentar a fonte para melhor acessibilidade */
}

.register-link:hover {
  text-decoration: underline;
}

.login-button:hover, .help-button:hover {
  background-color: #0288d1;
}

/* Responsividade para Dispositivos Móveis */
@media (max-width: 600px) {
  .user-icon {
    width: 80px; /* Ajusta o tamanho do logo em dispositivos móveis */
    height: 80px;
    margin-bottom: 15px;
  }

  .login-input {
    width: 100%;
    padding: 8px;
    font-size: 14px; /* Ajusta o tamanho da fonte para dispositivos móveis */
  }

  .login-button, .help-button, .accessibility-button {
    padding: 8px;
    font-size: 14px; /* Ajusta o tamanho da fonte para dispositivos móveis */
  }

  .forgot-password-link {
    font-size: 12px; /* Ajusta o tamanho da fonte para dispositivos móveis */
  }

  .register-link {
    font-size: 14px; /* Ajusta o tamanho da fonte para dispositivos móveis */
  }
}

/* Responsividade para Tablets */
@media (min-width: 601px) and (max-width: 900px) {
  .user-icon {
    width: 90px; /* Ajusta o tamanho do logo em tablets */
    height: 90px;
    margin-bottom: 18px;
  }

  .login-input {
    width: 90%;
    padding: 9px;
    font-size: 15px; /* Ajusta o tamanho da fonte para tablets */
  }

  .login-button, .help-button, .accessibility-button {
    padding: 9px;
    font-size: 15px; /* Ajusta o tamanho da fonte para tablets */
  }

  .forgot-password-link {
    font-size: 13px; /* Ajusta o tamanho da fonte para tablets */
  }

  .register-link {
    font-size: 15px; /* Ajusta o tamanho da fonte para tablets */
  }
}

/* Responsividade para Desktops */
@media (min-width: 901px) {
  .user-icon {
    width: 100px; /* Ajusta o tamanho do logo em desktops */
    height: 100px;
    margin-bottom: 20px;
  }

  .login-input {
    width: 80%;
    padding: 10px;
    font-size: 16px; /* Ajusta o tamanho da fonte para desktops */
  }

  .login-button, .help-button, .accessibility-button {
    padding: 10px;
    font-size: 16px; /* Ajusta o tamanho da fonte para desktops */
  }

  .forgot-password-link {
    font-size: 14px; /* Ajusta o tamanho da fonte para desktops */
  }

  .register-link {
    font-size: 16px; /* Ajusta o tamanho da fonte para desktops */
  }
}
