.resultados-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  margin: auto;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  text-align: center;
  font-family: 'Arial', sans-serif;
}

.resultados-container h1 {
  font-size: 32px;
  color: #333;
  margin-bottom: 30px;
  font-weight: bold;
}

.resultados-container .pontuacao {
  font-size: 24px;
  color: #555;
  margin-bottom: 20px;
}

.resultados-container .feedback {
  font-size: 18px;
  color: #666;
  margin-bottom: 20px;
  line-height: 1.6;
}

.resultados-container .recomendacoes h2 {
  font-size: 22px;
  color: #333;
  margin-bottom: 15px;
  font-weight: bold;
}

.resultados-container .recomendacoes ul {
  list-style-type: disc;
  padding-left: 20px;
}

.resultados-container .recomendacoes li {
  font-size: 18px;
  color: #333;
  margin-bottom: 10px;
}

.resultados-container .buttons-container {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-top: 20px;
}

.resultados-container .button {
  padding: 12px 25px;
  font-size: 18px;
  color: white;
  background-color: #007BFF;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.resultados-container .button:hover {
  background-color: #0056b3;
}
