.sidebar-container {
    width: 250px;
    height: 100vh;
    background-color: #4a626c;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    position: fixed;
    left: -250px; /* Escondido inicialmente */
    transition: left 0.3s ease-in-out;
  }
  
  .sidebar-container.open {
    left: 0; /* Visível quando aberto */
  }
  
  .sidebar-logo {
    width: 80px;
    height: 80px;
    margin-bottom: 20px;
    margin-left: 45px;
  }
  
  .sidebar-nav ul {
    list-style: none;
    padding: 0;
    width: 100%;
  }
  
  .sidebar-nav ul li {
    padding: 15px 20px;
    width: 100%;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }
  
  .sidebar-nav ul li:hover {
    background-color: #444;
  }

  /* Media Query para Dispositivos Móveis */ 
  @media (max-width: 600px) { 
    .sidebar-container { width: 100%; /* Sidebar ocupa toda a largura da tela em dispositivos móveis */ 
    } 
    .sidebar-logo { width: 50px; /* Ajusta o tamanho do logo em dispositivos móveis */ 
    } 
    .sidebar-nav ul li { padding: 10px; /* Ajusta o padding dos itens do menu em dispositivos móveis */ } 
  }
  