body.high-contrast {
  background-color: #000 !important;
  color: #fff !important;
}

body.large-text {
  font-size: 20px;
}

.app-container {
  display: flex;
  flex-direction: column; /* Altera a direção do layout para coluna */
}

.main-content {
  padding: 20px;
  width: 100%; /* Largura total */
  text-align: center; /* Centraliza o conteúdo */
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 10px 20px;
}

.hamburger-button {
  font-size: 24px;
  background: none;
  border: none;
  cursor: pointer;
  color: #007BFF;
  position: absolute;
  left: 20px;
}

.user-icon {
  width: 80px;
  height: 80px;
  margin-right: 20px;
  transition: none; /* Remove zoom */
}

.title {
  font-size: 32px;
  color: #333;
}

.intro-section {
  margin-bottom: 30px;
}

.description {
  font-size: 18px;
  color: #666;
}

.progress-section {
  margin-bottom: 30px;
}

.progress-bar-container {
  width: 100%; /* Ajustando a largura da barra de progresso */
  max-width: 600px;
  background-color: #ddd;
  border-radius: 10px;
  margin: 0 auto;
  overflow: hidden;
}

.progress-bar {
  height: 20px; /* Ajustando a altura da barra de progresso */
  background-color: #007BFF;
  text-align: center;
  line-height: 20px;
  color: white;
  font-weight: bold;
  transition: width 0.3s ease-in-out;
}

.tests-section {
  margin-bottom: 30px;
}

.section-title {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

.buttons-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.test-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 20px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  min-width: 150px;
  text-align: center;
  margin: 10px;
}

.test-button:hover {
  background-color: #0056b3;
}

.button-text {
  font-size: 18px;
  font-weight: bold;
}

.start-text {
  font-size: 14px;
}

.stats-section {
  margin-bottom: 30px;
}

.quick-stats {
  text-align: left;
  padding: 10px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
  width: 80%;
  margin: 0 auto;
}

.quick-stats h2 {
  margin-bottom: 10px;
  color: #333;
}

.quick-stats p {
  margin: 5px 0;
  color: #666;
}

.motivation-section {
  margin-bottom: 30px;
}

.motivation-message {
  background-color: #f1f1f1;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 15px;
  width: 80%;
  margin: 0 auto;
  text-align: left;
}

.motivation-message h2 {
  color: #333;
  margin-bottom: 10px;
}

.motivation-message p {
  color: #666;
  margin-bottom: 5px;
}

.motivation-message .professional {
  font-weight: bold;
  color: #333;
}

.motivation-message .date {
  font-size: 12px;
  color: #999;
}

.accessibility-options {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.accessibility-button {
  margin: 5px;
  padding: 10px;
  background-color: #29b6f6;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease-in-out;
}

.accessibility-button:hover {
  background-color: #0288d1;
}
