.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Altura total da viewport */
}

.pergunta-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  width: 100%; /* Para evitar que a largura ultrapasse 600px */
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out; /* Transição suave */
}

.pergunta-container.fade-out {
  opacity: 0; /* Efeito de desvanecimento */
  transform: translateX(-100px); /* Movimento suave para a esquerda */
}

.pergunta-container h1 {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
}

.pergunta-container .step-indicator {
  font-size: 16px;
  color: #999;
  margin-bottom: 20px;
}

.pergunta-container form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.pergunta-container label {
  font-size: 18px;
  color: #666;
  margin-bottom: 10px;
  text-align: center; /* Centraliza o texto da pergunta */
}

.radio-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.radio-label {
  display: block;
  width: 100%;
  max-width: 400px;
  padding: 10px;
  margin-bottom: 10px;
  text-align: center;
  background-color: #29b6f6; /* Azul especificado */
  color: white; /* Fonte branca */
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.radio-label:hover {
  background-color: #0288d1; /* Azul mais escuro para hover */
}

.radio-label input {
  display: none;
}

.radio-label input + span {
  color: white; /* Fonte branca sempre, independentemente do estado */
}

.radio-label input:checked + span {
  background-color: #0288d1; /* Azul mais escuro para indicar seleção */
}

.button-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 20px;
}

.button-container button {
  padding: 10px 20px;
  font-size: 16px;
  color: white; /* Fonte branca */
  background-color: #29b6f6;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.button-container button:hover {
  background-color: #0288d1;
}
