.estatisticas-rapidas {
  text-align: left;
  padding: 10px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
  width: 80%;
  margin: 0 auto;
}

.estatisticas-rapidas h2 {
  margin-bottom: 10px;
  color: #333;
}

.estatisticas-rapidas p {
  margin: 5px 0;
  color: #666;
}

@media (max-width: 600px) { 
  .estatisticas-rapidas { margin: 10px; padding: 15px; border-radius: 5px; 
  } 
  .estatisticas-rapidas h2 { font-size: 20px; margin-bottom: 10px;
  } 
  .estatisticas-detalhes { font-size: 16px; } 
}